body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #87898C;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.bed {
  width: 60px;
  height: 90px;
  margin-top: 5px;
  background: #3E4F59;
  border-radius: 5px;
  transition: all ease 1s;
  font-size: 25px;
  text-align: center;
  line-height: 90px;
  position: relative;
}

.hor {
  width: 90px;
  height: 60px;
  margin-top: 44px;
  line-height: 60px;
}

.hor:nth-child(1){
  margin-top: 10px;
}

.high{
  background: #0D1C26;
  color: white;
}

.ellipse {
  float: left;
  width: 25px;
  height: 25px;
  margin-left: 3px;
  shape-outside: ellipse(50% 20%);
  clip-path: ellipse(50% 20%);
  background: lightblue;
}

.hor .ellipse{
  float: none;
  position: relative;
  shape-outside: ellipse(20% 50%);
  clip-path: ellipse(20% 50%);
  margin-left: 0px;
  margin-top: 3px;
  top: 3px;
  
}

.reverse .ellipse {
  margin-top: 65px;
}

.text_child{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.parent_view{
  transition: all ease 200ms;
  -webkit-transition: all ease 200ms;
  -moz-transition: all ease 200ms;
  -o-transition: all ease 200ms;
  -ms-transition: all ease 200ms;
  opacity: 0;
}

.not_found_inv{
  background: #dc3545;
}

.text_child_id{
  font-size: 15px;
  white-space: nowrap;
  position: relative;
  left: -50px;
  text-align: start;
}

.reverse .text_child_id{
  top: 0px;
}

.hor .text_child_id{
  top: -50px;
  left: 20px;
}

.brand_img{
  width: 100%;
}
